  private davariable(cad:string,varx:string):string {
   var re="";
    var otro=cad
    var bb:any=[]
    bb=otro.split(varx)


      if(bb.length==0)
      {
        re="error"
      }
      else{
        var cad0=''
        cad0=bb[0]


        if(cad0.indexOf(';')<0)
        {
          re="error"

        }
        else
        {
          var cc:any=bb[1].split(';')
          var dd:any=cc[1]
          var ee:any=dd.split('&')
          re=ee[0];
        }


      }


    return re;
  }



